@font-face {
	font-family: 'Raleway';
	src: url("./fonts/Raleway-Light.ttf");
	font-weight: normal;
	font-style: normal
}
@font-face {
	font-family: 'Raleway-Medium';
	src: url("fonts/Raleway-Medium.ttf");
	font-weight: normal;
	font-style: normal
}

@font-face {
	font-family: 'Raleway-Bold';
	src: url("fonts/Raleway-Bold.ttf");
	font-weight: normal;
	font-style: normal
}

:root {



/* Colors: */
--unnamed-color-012169: #012169;
---67eb9: #60D1E0;
--unnamed-color-0098dd: #0098DD;
--unnamed-color-002e9d: #002E9D;
--unnamed-color-e5eaf4: #E5EAF4;
--unnamed-color-ffffff: #FFFFFF;
--unnamed-color-000000: #000000;
--unnamed-color-0032A0: #0032A0;
--unnamed-color-001865: #001865;


/* Font/text values */
--unnamed-font-family-raleway: Raleway;
--unnamed-font-family-raleway-medium: Raleway-Medium;
--unnamed-font-family-raleway-bold: Raleway-Bold;
--unnamed-font-style-normal: normal;
--unnamed-font-weight-300: 300px;
--unnamed-font-weight-normal: normal;
--unnamed-font-weight-bold: bold;
--unnamed-font-size-18: 18px;
--unnamed-font-size-20: 20px;
--unnamed-font-size-22: 22px;
--unnamed-font-size-24: 24px;
--unnamed-font-size-30: 30px;
--unnamed-character-spacing-0: 0px;
--unnamed-character-spacing--0-3: -0.3px;
--unnamed-character-spacing--0-18: -0.18px;
--unnamed-character-spacing--0-22: -0.22px;
--unnamed-line-spacing-20: 20px;
--unnamed-line-spacing-22: 22px;
--unnamed-line-spacing-24: 24px;
--unnamed-line-spacing-29: 29px;
}

/* Character Styles */
.mens {
font-family: var(--unnamed-font-family-raleway);
font-style: var(--unnamed-font-style-normal);
font-weight: var(--unnamed-font-weight-normal);
font-size: var(--unnamed-font-size-20);
line-height: var(--unnamed-line-spacing-24);
letter-spacing: var(--unnamed-character-spacing-0);
color: var(--unnamed-color-ffffff);
}
.unnamed-character-style-1 {
font-family: var(--unnamed-font-family-raleway);
font-style: var(--unnamed-font-style-normal);
font-weight: var(--unnamed-font-weight-bold);
font-size: var(--unnamed-font-size-18);
line-height: var(--unnamed-line-spacing-20);
letter-spacing: var(--unnamed-character-spacing--0-18);
color: var(--unnamed-color-002e9d);
}
.subttulo-2-raleway-bold {
font-family: var(--unnamed-font-family-raleway);
font-style: var(--unnamed-font-style-normal);
font-weight: var(--unnamed-font-weight-bold);
font-size: var(--unnamed-font-size-22);
line-height: var(--unnamed-line-spacing-22);
letter-spacing: var(--unnamed-character-spacing--0-22);
color: var(--unnamed-color-0098dd);
}
.cuerpos-de-texto-raleway-light {
font-family: var(--unnamed-font-family-raleway);
font-style: var(--unnamed-font-style-normal);
font-weight: var(--unnamed-font-weight-300);
font-size: var(--unnamed-font-size-20);
line-height: var(--unnamed-line-spacing-24);
letter-spacing: var(--unnamed-character-spacing-0);
color: var(--unnamed-color-000000);
}
.subttulos-raleway-medium {
font-family: var(--unnamed-font-family-raleway);
font-style: var(--unnamed-font-style-normal);
font-weight: var(--unnamed-font-weight-bold);
font-size: var(--unnamed-font-size-24);
line-height: var(--unnamed-line-spacing-29);
letter-spacing: var(--unnamed-character-spacing-0);
color: var(--unnamed-color-0098dd);
}
.titulares-raleway-light {
font-family: var(--unnamed-font-family-raleway);
font-style: var(--unnamed-font-style-normal);
font-weight: var(--unnamed-font-weight-300);
font-size: var(--unnamed-font-size-30);
line-height: var(--unnamed-line-spacing-20);
letter-spacing: var(--unnamed-character-spacing--0-3);
color: var(--unnamed-color-002e9d);
}