* {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box
}

*:before, *:after {
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box
}

html {
	font-family: Raleway;
	-webkit-text-size-adjust: 100%;
	-ms-text-size-adjust: 100%
}

body {
	background-color: #F1F1F1;
	margin: 0;
	font-family:Raleway;
	font-feature-settings: 'lnum' 1; 
}

a {
	cursor: pointer;
	display: inline-block;
	text-decoration: none
}

a:hover, a:active {
	outline: 0
}

a, button {
	transition: all 0.3s;
	-moz-transition: all 0.3s;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s
}

ul, ol {
	list-style: none;
	padding: 0;
	margin: 0
}

table {
	width: 100%;
	height: 100%;
	border-spacing: 0;
	border-collapse: collapse
}

button, input, optgroup, select, textarea {
	margin: 0;
	padding: 0;
	border: none;
	font: inherit;
	color: inherit
}

button, input[type="button"] {
	-webkit-appearance: none;
	-moz-appearance: none;
	appearance: none;
	cursor: pointer;
	display: block
}

img {
	max-width: 100%;
	border: 0
}

article, div, aside, footer, header, nav, section {
	display: block
}

hr {
	height: 0;
	-moz-box-sizing: content-box;
	box-sizing: content-box
}

.left {
	float: left
}

.right {
	float: right
}

.inline {
	display: inline-block
}

.relative, #wrapper, #canvas, section {
	position: relative
}

.absolute {
	position: absolute
}

.fixed {
	position: fixed;
	width: 100%
}

.hidden {
	display: none !important
}

.noscroll {
	overflow: hidden
}

.va-p {
	display: table;
	height: 100%;
	width: 100%
}

.va {
	display: table-cell;
	vertical-align: middle
}

.vab {
	display: table-cell;
	vertical-align: middle;
	vertical-align: bottom
}

.vat {
	display: table-cell;
	vertical-align: middle;
	vertical-align: top
}

.container, article {
	padding-right: 15px;
	padding-left: 15px;
	margin-right: auto;
	margin-left: auto;
	position: relative;
	width: 1024px;
	z-index: 10
}

#container{
	background-color: #0032A0;
}

.row {
	margin-right: -15px;
	margin-left: -15px
}

.tbl {
	display: table;
	width: 100%
}

.tbl>div[class*="col"] {
	float: none !important;
	display: table-cell
}

.tbl.bdsp {
	border-spacing: 15px;
	margin-top: -15px
}

.tbl.bdsp .clearfix {
	border-spacing: 0
}

.clearfix:before, .clearfix:after, article:before, article:after,
	.container:before, .container:after, .row:before, .row:after, .item:before,
	.item:after {
	display: table;
	content: " "
}

.clearfix:after, .container:after, article:after, .row:after, .item:after
	{
	clear: both
}

