.dk-select, .dk-select *, .dk-select *:before, .dk-select *:after, .dk-select-multi, .dk-select-multi *, .dk-select-multi *:before, .dk-select-multi *:after {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; 
}

.dk-select, .dk-select-multi {
  position: relative;
  z-index: 10;
  vertical-align: middle;
  font-size: 25px;
  cursor: pointer;
  display: inline-block;
}
footer .dk-select{ font-size: 14px; display: block; }
footer .dk-selected{ color: #008ED4; height: 17px; line-height: 17px; font-weight: 400; padding: 0 45px 0 0; }
footer .dk-selected:after {
  margin: -3px 15px 0 0;
  top: 50%;
  width: 16px; height: 7px; 
  background: url(../img/icons/sprite.png) -68px -189px no-repeat;
}
.sm .dk-select{ width: 100%; border: 1px solid #babcbe; background-color: #fff; }
.sm .dk-selected{ font-weight: 700; color: #777; padding-left: 15px; }
.sm .dk-select, .sm .dk-option{ font-size: 13px; }

.dk-selected {
  color: #000;
  overflow: hidden;
  position: relative;
  line-height: 35px;
  height: 35px;
  font-weight: 900;
  padding: 0 50px 0 0;
  -webkit-text-overflow: ellipsis;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis; 
}
.dblue .dk-selected{ color: #fff; }
.dk-selected:before, .dk-selected:after {
  content: '';
  display: block;
  position: absolute;
  right: 0; 
}
.dk-selected:after {
  margin: -5px 15px 0 0;
  top: 50%;
  width: 21px; height: 10px; 
  background: url(../img/icons/sprite.png) -46px -189px no-repeat;
}

.dk-selected-disabled {
  color: #333; 
}

.dk-select .dk-select-options {
  margin-top: -1px;
  position: absolute;
  display: none;
  left: 0; 
}

.dk-select-open-up .dk-select-options {
  margin-bottom: -1px;
  bottom: 100%; 
}

.dk-select-open-down .dk-select-options {
  top: 100%; 
}

.dk-select-multi .dk-select-options {
  max-height: 10em; 
}

.dk-select-options {
  width: 100%;
  z-index: 10;
  background-color: white;
  border: 1px solid #CCCCCC;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 200px;
  list-style: none;
  margin: 0; 
}

.dk-option-selected, .dk-select-options-highlight .dk-option-selected {
  background-color: #fff;
}
.dk-select-options .dk-option-highlight{
  background-color: #EEE;
}

.dk-option {
  color: #000;
  padding: 10px 20px; 
  font-size: 15px;
  border-top: 1px solid #e3e3e3;
}

.dk-select-options .dk-option-disabled {
  color: #0083ff;
  background-color: transparent; 
}

.dk-optgroup {
  border: solid #CCCCCC;
  border-width: 1px 0;
  padding: 0.25em 0;
  margin-top: 0.25em; 
}
.dk-optgroup + .dk-option {
  margin-top: 0.25em; 
}
.dk-optgroup + .dk-optgroup {
  border-top-width: 0;
  margin-top: 0; 
}
.dk-optgroup:nth-child(2) {
  padding-top: 0;
  border-top: none;
  margin-top: 0; 
}
.dk-optgroup:last-child {
  border-bottom-width: 0;
  margin-bottom: 0;
  padding-bottom: 0; 
}

.dk-optgroup-label {
  padding: 0 0.5em 0.25em;
  font-weight: bold;
  width: 100%; 
}

.dk-optgroup-options {
  list-style: none;
  padding-left: 0; 
}
.dk-optgroup-options li {
  padding-left: 1.2em; 
}

.dk-select-open-up, .dk-select-open-down{ z-index: 40; }

.dk-select-open-up .dk-selected {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
  border-color: #c8c8c8; 
  /*background-color: #FFF*/;
}
.dblue .dk-select-open-up .dk-selected, .dblue .dk-select-open-down .dk-selected {
  background-color: #0090D7;
}

.dk-select-open-down .dk-selected {
  /*background-color: #FFF*/
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
  border-color: #c8c8c8; 
}

.dk-select-open-up .dk-selected:before, .dk-select-open-down .dk-selected:before {  border-left-color: #c8c8c8; }

.dk-select-open-up .dk-select-options, .dk-select-open-down .dk-select-options, .d-verde .dk-select-multi:focus .dk-select-options {
  display: block;
  border-color: #c8c8c8; 
  border-top: none;
}

.dk-select-multi:hover, .dk-select-multi:focus {
  outline: none; 
}

.dk-selected:hover, .dk-selected:focus {
  outline: none;
}

.dk-select-disabled {
  opacity: 0.6;
  color: #0083ff;
  cursor: not-allowed; 
}
.dk-select-disabled .dk-selected:hover, .dk-select-disabled .dk-selected:focus {
  border-color: inherit; 
}
.dk-select-disabled .dk-selected:hover:before, .dk-select-disabled .dk-selected:focus:before {
  border-top-color: inherit; 
}
.dk-select-disabled .dk-selected:hover:before, .dk-select-disabled .dk-selected:focus:before {
  border-left-color: inherit; 
}
select[data-dkcacheid] {
  display: none; 
}
